<template>
  <div>
    <!-- 内容区域 -->
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>文章列表</span>
      </div>
      <!-- 筛选 -->
      <div class="search-box">
        <el-form :inline="true" :model="q" class="demo-form-inline">
          <el-form-item label="文章分类">
            <el-select v-model="q.cate_id" placeholder="请选择分类">
              <el-option
                v-for="obj in cateList"
                :key="obj.id"
                :label="obj.cate_name"
                :value="obj.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="发布状态">
            <el-select v-model="q.state" placeholder="请选择状态">
              <el-option label="已发布" value="已发布"></el-option>
              <el-option label="草稿" value="草稿"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="small" @click="choseFn"
              >筛选</el-button
            >
            <el-button type="info" size="small" @click="resetFn"
              >重置</el-button
            >
          </el-form-item>
        </el-form>
        <!-- 发表文章的按钮 -->
        <el-button
          type="primary"
          size="small"
          class="btn-pub"
          @click="showPubDialogFn"
        >
          发表文章
        </el-button>
      </div>

      <!-- 文章表格区域 -->
      <!-- artList数组：[{title，cate_name，pub_date，state},...] -->
      <el-table :data="artList" style="width: 100%" border stripe>
        <el-table-column label="文章标题" prop="title">
          <template v-slot="scope">
            <el-link type="primary" @click="showDetailFn(scope.row.id)">
              {{ scope.row.title }}
            </el-link>
          </template>
        </el-table-column>
        <el-table-column label="分类" prop="cate_name"></el-table-column>
        <el-table-column label="发表时间" prop="pub_date">
          <template v-slot="scope">
            <span>{{ $formatDate(scope.row.pub_date) }} </span>
          </template>
        </el-table-column>
        <el-table-column label="状态" prop="state"></el-table-column>
        <el-table-column label="操作">
          <template v-slot="{ row }">
            <el-button type="danger" size="mini" @click="removeFn(row.id)">
              删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页区域 -->
      <el-pagination
        @size-change="handleSizeChangeFn"
        @current-change="handleCurrentChangeFn"
        :current-page.sync="q.pagenum"
        :page-sizes="[2, 3, 5, 10]"
        :page-size.sync="q.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!-- 发表文章--对话框 -->
    <el-dialog
      title="发表文章"
      :visible.sync="pubDialogVisible"
      fullscreen
      :before-close="handleClose"
      @close="dialogCloseFn"
    >
      <el-form
        :model="pubForm"
        :rules="pubFormRules"
        ref="pubFormRef"
        label-width="100px"
      >
        <el-form-item label="文章标题" prop="title">
          <el-input v-model="pubForm.title" placeholder="请输入标题"></el-input>
        </el-form-item>
        <el-form-item label="文章分类" prop="cate_id">
          <el-select
            v-model="pubForm.cate_id"
            placeholder="请选择分类"
            style="width: 100%"
          >
            <!-- 因为整个表单要发给后台，去提前看一眼Vue代码里的值需要什么，发现接口文档需要的是分类id -->
            <el-option
              v-for="obj in cateList"
              :key="obj.id"
              :label="obj.cate_name"
              :value="obj.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- 富文本 -->
        <el-form-item label="文章内容" prop="content">
          <quill-editor
            @blur="contentChangeFn"
            v-model="pubForm.content"
          ></quill-editor>
        </el-form-item>
        <el-form-item label="文章封面" prop="cover_img">
          <!-- 用来显示封面的图片 -->
          <img
            src="../../assets/images/cover.jpg"
            alt=""
            class="cover-img"
            ref="imgRef"
          />
          <br />
          <!-- 文件选择框，默认被隐藏 -->
          <input
            type="file"
            style="display: none"
            accept="image/*"
            ref="iptFileRef"
            @change="changeCoverFn"
          />
          <!-- 选择封面的按钮 -->
          <el-button type="text" @click="selCoverFn">+ 选择封面</el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="pubArticleFn('已发布')">
            发布
          </el-button>
          <el-button type="info" @click="pubArticleFn('草稿')">
            存为草稿
          </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!-- 查看文章详情的对话框 -->
    <el-dialog title="文章预览" :visible.sync="detailVisible" width="80%">
      <h1 class="title">{{ artDetail.title }}</h1>

      <div class="info">
        <span>作者：{{ artDetail.nickname || artDetail.username }}</span>
        <span>发布时间：{{ $formatDate(artDetail.pub_date) }}</span>
        <span>所属分类：{{ artDetail.cate_name }}</span>
        <span>状态：{{ artDetail.state }}</span>
      </div>
      <!-- 分割线 -->
      <el-divider></el-divider>

      <!-- 文章的封面 -->
      <img
        v-if="artDetail.cover_img"
        :src="baseURLR + artDetail.cover_img"
        alt=""
      />

      <!-- 文章的详情 -->
      <!--v-html：把字符串当标签解析  -->
      <div v-html="artDetail.content" class="detail-box"></div>
    </el-dialog>
  </div>
</template>

<script>
import { getArtCateListAPI, uploadArticleAPI, getArtListAPI, getArtDetailAPI, DelArtAPI } from '@/api'
// 标签样式中，引入图片文件可以写路径，
// 在js里引入图片要用import引入
// webpack会把图片变为一个base64字符串/在打包后图片的临时地址
import imgObj from '../../assets/images/cover.jpg'
// 引入
import { baseURL } from '@/utils/request'
export default {
  name: 'artList',
  data () {
    return {
      // 查询参数对象
      q: {
        pagenum: 1, // 默认那第一页数据
        pagesize: 5, // 默认当前页需要几条数据（传递给后台，后台就返回几个数据）
        cate_id: '',
        state: ''
      },
      pubDialogVisible: false, // 控制发表文章对话框的显示与隐藏
      pubForm: { // 表单的数据对象
        title: '', // 文章标题
        cate_id: '', // 文章分类id
        content: '', // 文章内容
        cover_img: '', // 文章封面（文件）
        state: ''// 文章发布状态，可选值有两个：草稿，已发布
      },
      pubFormRules: { // 表单的验证规则对象
        title: [
          { required: true, message: '请输入文章标题', trigger: 'blur' },
          { min: 1, max: 30, message: '文章标题的长度为1-30个字符', trigger: 'blur' }
        ],
        cate_id: [{ required: true, message: '请选择文章标题', trigger: 'change' }],
        content: [{ required: true, message: '请输入文章内容', trigger: 'change' }],
        cover_img: [{ required: true, message: '请选择封面', trigger: 'change' }]
      },
      cateList: [], // 保存文章分类列表
      artList: [], // 保存文章列表
      total: 0, // 保存现有文章总数
      detailVisible: false, // 用于查看文章详情的对话框的（显示和隐藏）
      artDetail: [], //
      baseURLR: baseURL// 服务器前缀地址
    }
  },
  methods: {
    // 对话框关闭前的回调
    // 注意：
    // 自带三种关闭的方式：只要dialog自带的右上角x，和按下esc按键，和点击半透明蒙层关闭才会走这里
    // 自己调用点击设置变量pubDialogVisible为false，也会导致关闭前的回调触发
    async handleClose (done) { // 调用就关闭对话框
      // 询问用户是否确认关闭对话框
      const confirmResult = await this.$confirm('此操作将导致文章信息丢失, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)// await .catch

      // 取消了关闭-阻止住, 什么都不干  对话框不能关闭，提前return不让下面done执行
      if (confirmResult === 'cancel') return
      // 确认关闭
      done()
    },
    // 发布文章按钮-点击事件-让添加文章对话框显示
    showPubDialogFn () {
      this.pubDialogVisible = true
    },

    // 选择封面点击事件--让文件选择窗口出现
    selCoverFn () {
      this.$refs.iptFileRef.click()// 使用js来模拟一次点击事件动作
    },
    // 用户选择了封面文件
    changeCoverFn (e) {
      // e.target拿到触发事件的标签（input标签对象本身）
      // e.target.files拿到选择的文件数组
      const files = e.target.files
      // console.log(files)
      if (files.length === 0) {
        // 用户没有选择图片，那得把cover_img属性置空
        this.pubForm.cover_img = null
        // img要显示会默认的cover.png
        this.$refs.imgRef.setAttribute('src', imgObj)
      } else {
        // 用户选择了图片，把文件直接保存到表单对象的属性里
        this.pubForm.cover_img = files[0]
        // 把图片文件，要显示到img标签里
        const url = URL.createObjectURL(files[0])
        this.$refs.imgRef.setAttribute('src', url)
      }
      // 让封面表单走校验规则
      this.$refs.pubFormRef.validateField('cover_img')
    },
    // 表单里（点击发布/存成草稿）按钮点击事件->准备调用后端接口
    pubArticleFn (str) {
      // str的值 "已发布"或者是"草稿"（后端要求的参数）
      this.pubForm.state = str// 保存到统一的表单对象上

      // 兜底校验
      this.$refs.pubFormRef.validate(async valid => {
        if (valid) {
          // 都通过效验
          // FromData类是H5新出的专门为了装文件内容和其他参数的一个容器
          const fd = new FormData()// 准备一个表单数据对象的容器
          //   fd.append('参数名',值)
          fd.append('title', this.pubForm.title)
          fd.append('cate_id', this.pubForm.cate_id)
          fd.append('content', this.pubForm.content)
          fd.append('cover_img', this.pubForm.cover_img)
          fd.append('state', this.pubForm.state)
          const { data: res } = await uploadArticleAPI(fd)
          console.log(res)
          if (res.code !== 0) return this.$message.error('发布文章失败！')
          this.$message.success('发布文章成功！')
          // 关闭对话框
          this.pubDialogVisible = false
          // 刷新文章列表
          this.getArtCateListFn()
        } else {
          return false// 阻止默认行为
        }
      })
    },
    // 富文本编辑器内容改变触发事件
    contentChangeFn () {
      // 目标：如何让el-for校验，只校验content这个规则？
      this.$refs.pubFormRef.validateField('content')
    },
    // 新增文章--对话框关闭--清空表单
    dialogCloseFn () {
      // 清空表单
      this.$refs.pubFormRef.resetFields()
      // 封面不清空，这个时候需要手动给封面标签重新设置一个值，因为它没有受到v-model影响
      // 重设初始值
      this.$refs.imgRef.setAttribute('src', imgObj)
    },
    // 获取所有分类
    async getCateFn () {
      const { data: res } = await getArtCateListAPI()
      this.cateList = res.data
      console.log(res)
    },
    // 获取所有文章列表
    async getArtCateListFn () {
      const { data: res } = await getArtListAPI(this.q)
      this.artList = res.data
      this.total = res.total
      console.log(res)
    },
    // 分页--每页条数改变时触发
    // 核心思想：根据选择的页码/条数，影响q对象对应属性的值，再重新发一次请求让后台重新返回数据
    handleSizeChangeFn (sizes) {
      console.log('每页条数改变', sizes)
      // sizes：当前需要每页显示的条数
      // 因为Pagination的标签上已经加了.sync,子组件内会双向绑定到右侧vue变量上
      // （q对象里pagesize已经改变了）
      // 你不放心再写一遍
      this.q.pagesize = sizes// 可省略
      this.q.pagenum = 1
      this.getArtCateListFn()
    },
    // 当前页码改变时触发
    handleCurrentChangeFn (nowPages) {
      console.log('页码', nowPages)
      // nowPages:当前要看的第几页，页数
      this.getArtCateListFn()
    },
    // 筛选按钮--点击事件
    choseFn () {
      // 目的：当有了筛选的条件，我想让页码回归1，每页的条数回归2
      this.q.pagenum = 1
      this.q.pagesize = 2
      this.getArtCateListFn()
    },
    // 重置按钮--点击事件2
    resetFn () {
      this.q.pagenum = 1
      this.q.pagesize = 10
      this.q.cate_id = ''
      this.q.state = ''
      this.getArtCateListFn()
    },
    // 文章标题点击事件--查看详情
    async showDetailFn (artId) {
      this.detailVisible = true// 让详情的对话框出现
      // artId：文章id值
      const { data: res } = await getArtDetailAPI(artId)
      console.log(res)
      this.artDetail = res.data
    },
    // 删除文章按钮--点击事件
    async removeFn (artId) {
      const { data: res } = await DelArtAPI(artId)
      if (res.code !== 0) return this.$message.error(res.message)
      this.$message.success('删除文章成功')
      console.log(res)
      // 问题:在最后一页,删除最后一条时,虽然页码可以回到上一页,但是数据没有出现
      // 原因:发现参数pagenum的值不会自己回到上一页,因为你的代码没有修改过这个pagenum的值
      // 只是调用了getArtticleFn的方法，带着之前的参数请求去了，所以没有数据
      // 解决：当前页只剩一条数据时，进行pagenum--
      if (this.artList.length === 1) {
        if (this.q.pagenum > 1) { // 保证pagenum最小值为1
          this.q.pagenum--
        }
      }

      // 把分页和筛选的条件重置，让表格的数据重新请求一次
      // this.resetFn()
      // 或者不重置条件，直接请求数据更新
      this.getArtCateListFn()
    }
  },
  created () {
    // 请求文章分类数据
    this.getCateFn()
    // 请求文章列表
    this.getArtCateListFn()
  }
}
</script>

<style lang="less" scoped>
// scope属性的作用：让style里的选择器，只能选中当前组件的标签
// 原理：(多加一个data-v属性选择器)
// webpack打包的时候，会给 组件标签上 添加相同的data-v-hash值，然后也会给所有的选择器后面
// 加上一个[data-v-hash]值的属性选择器
// <标签 data-v-390246 class="my_a"></标签>
// 选择器会变成.my_a[data-v-390246]
// 重要注意事项：scoped只会给当前组件所有的原生标签添加data-v-hash值属性，还会给组件标签内根标签添加data-v-hash值属性
// 而组件内的标签不会添加
.search-box {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  .btn-pub {
    margin-top: 5px;
  }
}
// 设置富文本编辑器的默认最小高度
// ::v-deep作用: 穿透选择, 正常style上加了scope的话, 会给.ql-editor[data-v-hash]属性, 只能选择当前页面标签或者组件的根标签
// 如果想要选择组件内的标签(那些标签没有data-v-hash值)所以正常选择选不中, 加了::v-deep空格前置的话, 选择器就会变成如下形式
// [data-v-hash] .ql-editor 这样就能选中组件内的标签的class类名了
::v-deep .ql-editor {
  // 最小高度：标签本身的高度考内容撑开，但是无内容没有300高度，标签会设置最小高度为300px
  // 如果内容大于300px，标签高度也会随着撑开（比300px大）
  // 直接给height：那么无论容器内的内容有多少，超出300高度的内容会溢出外面而不是撑开此容器
  min-height: 300px;
}
// .ql-editor{}
// 上面这样写，选不中富文本组件内的标签的
// 原因：你写在这里会被在后面加上[data-v-hash]属性选择器，而它对应的哪个标签组件内标签，scoped又不会给他加入
// 解决：Vue提供了一个::v-deep样式语法，设置后，可以把属性选择器被自动添加到左侧

// 选择文件封面
.cover-img {
  width: 400px;
  height: 280px;
  object-fit: cover;
}
.title {
  font-size: 24px;
  text-align: center;
  font-weight: normal;
  color: #000;
  margin: 0 0 10px 0;
}

.info {
  font-size: 12px;
  span {
    margin-right: 20px;
  }
}

// 修改 dialog 内部元素的样式，需要添加样式穿透
::v-deep .detail-box {
  img {
    width: 500px;
  }
}
</style>
